import { Controller } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
import { ITableSelection } from '../types';
import calcTableSelectionPosUtil from '../utils/calcTableSelectionPos';

interface Payload {
  tblSelection: ITableSelection,
  table: Table,
  tableNode:  HTMLTableElement,
  scale?: number,
}

export default function calcTableSelectionPos(controller: Controller, payload: Payload) {
  if (!controller) return null;

  return calcTableSelectionPosUtil(payload);
}
